<template>
  <div style="height: 50px; line-height: 50px; border-bottom: 1px solid #ccc; display: flex; background-color: #545c64;
        border-color: #545c64;
    ">
    <div style="width: 200px; padding-left: 30px; font-weight: bold; color: dodgerblue;">仓库管理系统</div>
    <div style="flex: 1"></div>
    <div style="width: 100px; padding-right: 30px">
      <el-dropdown>
        <span class="el-dropdown-link" style="color: dodgerblue;cursor: pointer;" >
          {{ user.userName }}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <template #dropdown>
          <el-dropdown-menu>
            <el-dropdown-item @click="personal">个人信息</el-dropdown-item>
            <el-dropdown-item @click="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </template>
      </el-dropdown>
    </div>
    <el-dialog title= "个 人 信 息" v-model="dialogVisible" width="30%">
      <el-form :model="form" label-width="120px" :rules="rules">
        <el-form-item label="账号" prop="userName">
          <el-input v-model="form.userName" style="width: 80%"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="userPassword">
          <el-input type="password" v-model="form.userPassword" style="width: 80%" show-password></el-input>
        </el-form-item>
        <el-form-item label="权限" prop="role">
          <el-input v-model="form.role" style="width: 80%" v-bind:disabled= "disabled" ></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
    <span class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="update">修 改</el-button>
    </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import request from "../utils/request";
export default {
  name: "Header",
  data(){
    return {
      user : JSON.parse(sessionStorage.getItem('user')),
      form:{},
      disabled : true,
      dialogVisible : false,
      rules: {
        userName: [
          {required: true, message: '请输入用户名', trigger: 'blur'}
        ],
        userPassword: [
          {required: true, message: '请输入密码', trigger: 'blur'}
        ],
        role: [
          {required: true, message: '权限不能为空', trigger: 'blur'}
        ],
      },
    }
  },
  methods: {
    update(){
      request.put("/api/user/update", this.form).then(res => {
        if (res.code === 20001) {
          this.$message({
            message: res.message,
            type: 'error'
          })
        } else {
          this.$message({
            message: res.message,
            type: 'success'
          })
          this.dialogVisible = false;
        }
      })
    },
    personal(){
      console.log(this.user)
      this.dialogVisible = true
      this.form = JSON.parse(sessionStorage.getItem('user'))
    },
    logout(){
      this.$confirm('此操作将注销登录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        sessionStorage.removeItem("user")
        this.$router.push('/login')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消操作'
        });
      });
    }
  }
}
</script>

<style scoped>

</style>
