import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import locale from "element-plus/lib/locale/lang/zh-cn"


import '@/assets/css/global.css'

createApp(App).use(store).use(router).use(ElementPlus, {locale, size: 'small'}).use(ElementPlus).mount('#app')

// 挂载路由导航守卫
// to 将要访问的路径
// from 代表从哪个路径跳转而来
// next 是个函数，表示放行 next() 放行  next('/login') 强制跳转
router.beforeEach((to, from, next) => {
    // 如果用户访问的登录页或注册页，直接放行
    if (to.path === '/login' || to.path === '/register') return next();
    // 取出sessionStorage里面缓存的用户账号
    const userJson = sessionStorage.getItem("user")
    // sessionStorage中没有数据就表示用户未登录，强制跳转到登录页
    if (!userJson) return next('/login')
    next()
})

