import { createRouter, createWebHistory } from 'vue-router'
import Layout from '../layout/Layout.vue'

const routes = [
  {
    path: '/',
    name: 'Layout',
    component: Layout,
    redirect: "/login",
    children: [
      {
        path: 'user',
        name: 'User',
        component: () => import("../views/User"),
      },
      {
        path: 'index',
        name: 'Index',
        component: () => import("../views/Index"),
      },
      {
        path: 'sales',
        name: 'Sales',
        component: () => import("../views/Sales"),
      },
      {
        path: 'shipment',
        name: 'Shipment',
        component: () => import("../views/Shipment"),
      },
      {
        path: 'cargo',
        name: 'Cargo',
        component: () => import("../views/Cargo"),
      },
      {
        path: 'outInput',
        name: 'OutInput',
        component: () => import("../views/OutInput"),
      }
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import("../views/Login")
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import("../views/Register")
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
